/**
*   ===========================
*       Topics area
*   ===========================
*/

.categories {
    background-color: #FFFFFF;
    padding-bottom: 2rem;
    padding-top: 1rem;
    display: none;
    ul {
        padding: 0;
        margin: 0;
        display: grid;
        grid-template-columns: repeat(auto-fit, 200px);
        grid-gap: 5px;
        text-align: center;
        justify-content: center;
        list-style-type: none;
        li {
            padding: 0;
            margin: 0;
            display: flex;
            align-items: stretch;
            border: 1px solid #222D3C;
            border-radius: 3px;
            a {
                flex: 1;
                align-items: stretch;
                padding: 15px;
            }
            &:hover {
                border: 1px solid $theme__color-main_red;
                a {
                    color: $theme__color-main_red;
                }
            }
            .cat-posts-count {
                position: relative;
                flex: 0 1 38px;
                align-content: center;
                font-size: .7em;
                background-color: $theme__color-main_red;
                sup {
                    font-size: 100%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    -webkit-transform: translateX(-50%);
                    -o-transform: translateX(-50%);
                    transform: translateX(-50%);
                    color: #FFFFFF;
                }
            }
            @media (max-width: $screen__md) {
                &:hover {
                    border: 1px solid $theme__color-main_red;
                    color: $theme__color-main_red;
                }
            }
        }
    }
    a {
        font-size: .75em;
    }
}

.visible {
    display: grid;
}


