/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #37474F;
	font-family: "Varela Round", "Lucida Sans", Arial, sans-serif;
	font-size: 19.8px;
	font-size: 1.2375rem;
	line-height: 1.5;
}

@media screen and (min-width: 700px) {
	body,
	button,
	input,
	select,
	textarea {
		font-size: 22px;
		font-size: 1.375rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: "Varela Round", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
	color: #546E79;
}

h1 {
	font-size: 3rem;
}

h2 {
	font-size: 2.6rem;
}

h3 {
	font-size: 2.2rem;
}

h4 {
	font-size: 2rem;
}

h5 {
	font-size: 1.8rem;
}

h6 {
	font-size: 1.6rem;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 20.625px;
	font-size: 1.28906rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 20.625px;
	font-size: 1.28906rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

blockquote, q {
	quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
	content: "";
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

.fp-events {
	padding: 1em;
	background: rgba(0, 0, 0, 0.05);
}

.cptul-stripe-2 {
	max-width: 90%;
	padding: 0;
	margin: 1em auto;
	list-style-type: none;
}

.cptli-stripe-2--wrap {
	box-shadow: 5px 6px 10px 0px rgba(0, 0, 0, 0.15);
	margin-bottom: 1em;
}

.cptli-stripe-2--wrap .cptli-stripe-2--header {
	padding: 1em;
	background: rgba(0, 0, 0, 0.05);
}

.cptli-stripe-2--wrap .cptli-stripe-2--data {
	margin-bottom: .5em;
	padding: 1em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	text-decoration: none;
	color: #F74C4C;
}

a:hover {
	color: #87CCE5;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.menu {
	padding: 0;
	margin: 0;
}

/**
*   ===========================
*       Menu area
*   ===========================
*/
.main-nav {
	clear: both;
	padding-top: 1rem;
	padding-bottom: 1rem;
	background-color: #F74C4C;
}

.main-nav ul {
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(auto-fit, 160px);
	grid-gap: 5px;
	text-align: center;
	justify-content: center;
	list-style-type: none;
}

.main-nav ul li {
	padding: 5px 0;
	border-radius: 3px;
}

.main-nav ul li:hover {
	background-color: #FA5D5B;
}

@media (max-width: 768px) {
	.main-nav ul li {
		border: 1px solid #FA5D5B;
		padding: 10px;
	}
}

.main-nav a {
	color: #ECEFF1;
	font-size: 18px;
}

/**
*   ===========================
*      Topics toggle
*   ===========================
*/
.btn-topics-container {
	position: relative;
	height: 50px;
	overflow: hidden;
	border-top: 10px solid #F74C4C;
}

.btn-topics-container .btn-topics-wrap {
	text-align: center;
	position: absolute;
	top: -115px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-o-transform: translateX(-50%);
	transform: translateX(-50%);
	background: none;
}

.btn-topics-container .btn-topics-wrap .btn-toggle-topics {
	position: relative;
	display: flex;
	width: 150px;
	height: 150px;
	background-color: #F74C4C;
	text-shadow: none;
	border: 0;
	border-radius: 100%;
}

.btn-topics-container .btn-topics-wrap .btn-toggle-topics:hover {
	background: #F74C4C;
}

.btn-topics-container .btn-topics-wrap .btn-toggle-topics span svg {
	position: absolute;
	bottom: 8px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-o-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 30px;
	height: 30px;
}

/**
*   ===========================
*       Topics area
*   ===========================
*/
.categories {
	background-color: #FFFFFF;
	padding-bottom: 2rem;
	padding-top: 1rem;
	display: none;
}

.categories ul {
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(auto-fit, 200px);
	grid-gap: 5px;
	text-align: center;
	justify-content: center;
	list-style-type: none;
}

.categories ul li {
	padding: 0;
	margin: 0;
	display: flex;
	align-items: stretch;
	border: 1px solid #222D3C;
	border-radius: 3px;
}

.categories ul li a {
	flex: 1;
	align-items: stretch;
	padding: 15px;
}

.categories ul li:hover {
	border: 1px solid #F74C4C;
}

.categories ul li:hover a {
	color: #F74C4C;
}

.categories ul li .cat-posts-count {
	position: relative;
	flex: 0 1 38px;
	align-content: center;
	font-size: .7em;
	background-color: #F74C4C;
}

.categories ul li .cat-posts-count sup {
	font-size: 100%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-o-transform: translateX(-50%);
	transform: translateX(-50%);
	color: #FFFFFF;
}

@media (max-width: 768px) {
	.categories ul li:hover {
		border: 1px solid #F74C4C;
		color: #F74C4C;
	}
}

.categories a {
	font-size: .75em;
}

.visible {
	display: grid;
}

/**
*   ===========================
*       OLD STUFF
*   ===========================
*/
/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 19.25px;
	font-size: 1.20312rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Primary Styles
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 .5em;
}

.byline,
.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/**
    MAIN LAYOUT
    ------------------------------------------
 */
.index-content__container {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
	padding: 2.5px;
}

@media screen and (max-width: 640px) {
	.index-content__container {
		grid-template-columns: 1fr;
		padding: 0.5em 0;
	}
}

.article-card__container {
	display: grid;
	grid-gap: 10px;
	padding: 5px;
}

@media screen and (max-width: 640px) {
	.article-card__container {
		grid-template-columns: 1fr;
		padding-bottom: .5em;
	}
}

.article-card__container svg {
	fill: #F74C4C;
}

.page_content__container {
	max-width: 40em;
	padding: 0 1em;
	margin: 0 auto;
}

/*--------------------------------------------------------------
## Front Page
--------------------------------------------------------------*/
.events-post__stats,
.article-cards__front_page {
	display: grid;
	grid-gap: 5px;
	padding: 5px 5px 0 5px;
	grid-template-columns: repeat(3, 1fr);
}

@media screen and (max-width: 1024px) {
	.events-post__stats,
	.article-cards__front_page {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media screen and (max-width: 768px) {
	.events-post__stats,
	.article-cards__front_page {
		grid-template-columns: 1fr;
		margin-bottom: 0;
	}
}

.article-cards__front_page .article-card__wrapper {
	background-color: #F74C4C;
	padding: 1em;
	display: grid;
	border-radius: 3px;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page {
	box-shadow: none;
	display: grid;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header {
	padding-bottom: 1em;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-post__posted_on {
	padding: 0;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-post__posted_on .meta-posted_on {
	display: block;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-title {
	font-size: 1.4em;
	padding: 0;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-title a {
	color: #FFF;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-title a:hover {
	color: #FFCFC9;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-post__stats ul {
	margin: 0;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__header .article-post__stats svg {
	fill: #FFF;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__excerpt {
	padding-bottom: 1em;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__excerpt a {
	color: #FFF;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer {
	align-self: self-end;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer ul {
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
	grid-gap: 5px;
	text-align: center;
	list-style-type: none;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer ul li {
	padding: 0;
	margin: 0;
	align-items: stretch;
	border: 1px solid #FA5D5B;
	border-radius: 3px;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer ul li:hover {
	background-color: #FA5D5B;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer ul li a {
	color: #FFF;
	font-size: 0.75em;
}

.article-cards__front_page .article-card__wrapper .article-card__front_page .article-card__footer ul li .cat-posts-count sup {
	font-size: .6em;
	color: #FFF;
}

.article-cards__front_page .hp-featured {
	background-color: #3569B2;
}

.article-cards__front_page .hp-featured .article-card__front_page .article-card__footer ul li {
	border: 1px solid #FFF;
}

.article-cards__front_page .entry-date,
.article-cards__front_page .nameWhat,
.article-cards__front_page .article-card__excerpt,
.article-cards__front_page ul li,
.article-cards__front_page a {
	color: #FFF;
}

.events-post__stats ul {
	margin: 0;
}

.events-post__stats svg {
	fill: #FFF;
}

/*--------------------------------------------------------------
## Article Single
--------------------------------------------------------------*/
/**
* =============================
* SINGLE POST
* =============================
*/
.article-header {
	display: block;
	padding: 1em;
	background-color: #ECEFF1;
}

.article-header .single-header__wrapper {
	max-width: 1024px;
	margin: 0 auto;
}

.article-header .single-header__wrapper .special-post-data {
	margin-bottom: 5px;
}

.article-header .single-header__wrapper .special-post-data .special-data {
	display: inline-block;
	margin: 0 5px;
	margin-left: 0;
	padding: 0 15px;
	border-radius: 3px;
	background-color: #607D8B;
	color: #EEE;
	font-size: .5em;
	text-transform: uppercase;
}

.article-header .single-header__wrapper .special-post-data .special-data p {
	margin: 0;
}

.article-header .single-header__wrapper .article-title {
	margin-top: .5em;
	margin-bottom: .3em;
	line-height: 1em;
	font-size: 2em;
}

.article-post__stats ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.article-post__stats ul .article-post__event-town,
.article-post__stats ul .article-post__event-date,
.article-post__stats ul .article-post__comments_count,
.article-post__stats ul .article-post__reading_time {
	font-size: .8em;
	display: inline-block;
	padding-right: 15px;
}

.article-post__stats ul .article-post__event-town .article-post__stats-icon,
.article-post__stats ul .article-post__event-date .article-post__stats-icon,
.article-post__stats ul .article-post__comments_count .article-post__stats-icon,
.article-post__stats ul .article-post__reading_time .article-post__stats-icon {
	display: inline-block;
	vertical-align: middle;
	padding-right: 5px;
}

.article-post__stats ul .article-post__event-town .article-post__stats-icon svg,
.article-post__stats ul .article-post__event-date .article-post__stats-icon svg,
.article-post__stats ul .article-post__comments_count .article-post__stats-icon svg,
.article-post__stats ul .article-post__reading_time .article-post__stats-icon svg {
	margin: 0 auto;
	width: 18px;
	height: 18px;
	fill: #F74C4C;
}

.article-post__posted_on .meta-posted_on {
	font-size: .8em;
	display: block;
}

.article-post__posted_on .meta-posted_on a {
	color: #607D8B;
}

.article-post__posted_on .meta-author {
	display: inline-block;
	color: #90A4AE;
}

.index-image img {
	width: 100%;
	max-height: 400px;
	object-fit: cover;
}

.article-content {
	max-width: 40em;
	padding: 0 1em;
	margin: 0 auto;
}

.article-content .article-footer__categories {
	margin-bottom: 2em;
	clear: both;
	padding-top: 3em;
}

.article-content .article-footer__categories ul {
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: repeat(auto-fit, 150px);
	grid-gap: 5px;
	text-align: center;
	list-style-type: none;
}

.article-content .article-footer__categories ul li {
	padding: 0;
	margin: 0;
	align-items: stretch;
	border: 1px solid #EEE;
	border-radius: 3px;
}

.article-content .article-footer__categories ul li:hover {
	border-color: #B0BEC5;
}

.article-content .article-footer__categories ul li:hover a {
	color: #F74C4C;
}

.article-content .article-footer__categories ul li:hover .cat-posts-count sup {
	color: #B0BEC5;
